





















import Vue from 'vue';
import { Component, Prop } from 'vue-property-decorator';

@Component
export default class AccordionAddButton extends Vue {
  @Prop({ required: true }) size!: number;

  get buttonSize() {
    return Math.floor(this.size * 0.65);
  }

  get fontSize() {
    return Math.floor(this.buttonSize);
  }
}
